.button {
  display: inline-block;
  position: relative;
  width: 12.625rem;
  padding: 1rem 5.5rem 1rem 2.3rem;
  background-color: #408F95;
  font-family: "Gotu", sans-serif;
  font-size: 0.875rem;
  color: #fff;
  line-height: 1.5;
  letter-spacing: -0.01em;
  white-space: nowrap;
  border: 1px solid #408F95;
  /*overflow: hidden;*/
  z-index: 1;
}

.button--white {
  background-color: #fff;
  color: #408F95;
  border: 1px solid #fff;
}

/*ボタンの影*/
.button::before {
  content: "";
  display: inline-block;
  position: absolute;
  top: 0.25rem;
  left: 0.25rem;
  width: 12.625rem;
  height: 3.375rem;
  border: 1px solid #408F95;
  z-index: -2;
}

.button--white::before {
  border: 1px solid #fff;
}

/* ホバー時 */
@media screen and (min-width: 768px) {
  .button::after {
    display: inline-block;
    background-color: #fff;
    position: absolute;
    top: 0;
    left: 0;
    content: "";
    width: 100%;
    height: 100%;
    transform: scale(0, 1);
    transform-origin: left top;
    transition: 0.2s cubic-bezier(0.45, 0, 0.55, 1);
    z-index: -1;
  }
}

@media screen and (min-width: 768px) {
  .button--white::after {
    background-color: #408F95;
  }
}

@media screen and (min-width: 768px) {
  .button:hover {
    color: #408F95;
  }
}

@media screen and (min-width: 768px) {
  .button--white:hover {
    color: #fff;
  }
}

@media screen and (min-width: 768px) {
  .button:hover::after {
    transform: scale(1, 1);
  }
}

@media screen and (min-width: 768px) {
  .button:hover span::after {
    border-bottom: 1px solid #408F95;
    border-right: 1px solid #408F95;
  }
}

@media screen and (min-width: 768px) {
  .button--white:hover span::after {
    border-bottom: 1px solid #fff;
    border-right: 1px solid #fff;
  }
}

/* 矢印 */
.button span::after {
  content: "";
  display: inline-block;
  position: absolute;
  bottom: 45%;
  width: 2.5rem;
  height: 0.3125rem;
  border-bottom: 1px solid #fff;
  border-right: 1px solid #fff;
  transform: translateY(-50%) skew(45deg);
  margin-left: 0.5rem;
}

.button--white span::after {
  border-bottom: 1px solid #408F95;
  border-right: 1px solid #408F95;
}

