[type=checkbox] {
  display: none;
}
[type=checkbox] + span {
  display: inline-block;
  position: relative;
  padding: 0 0 0 1.875rem;
  margin: 0 1.25rem 0 0;
  font-size: 1rem;
  line-height: 1;
  cursor: pointer;
  transition: 0.3s ease;
}
@media screen and (min-width: 768px) {
  [type=checkbox] + span {
    padding: 0 0 0 1.375rem;
  }
}
[type=checkbox] + span::before {
  position: absolute;
  content: "";
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  background: #fff;
  border: 1px solid #408F95;
  width: 1.375rem;
  height: 1.375rem;
  display: block;
}
@media screen and (min-width: 768px) {
  [type=checkbox] + span::before {
    width: 0.875rem;
    height: 0.875rem;
  }
}
[type=checkbox] + span::after {
  opacity: 0;
  transition: 0.3s ease;
}
[type=checkbox]:checked + span::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  display: block;
  opacity: 1;
  /*チェックマークじゃない場合は以下を変更する*/
  transform: translateY(-50%) rotate(-45deg);
  width: 0.825rem;
  height: 0.4125rem;
  margin-top: -0.1em;
  margin-left: 0.2em;
  border-left: 2px solid #f00;
  border-bottom: 2px solid #f00;
}

select {
  /*↓リセットCSS */
  border-radius: 0;
  font-family: inherit;
