.hamburger-button {
  position: relative;
  width: 2rem;
  height: 1.5rem;
  z-index: 1000;
  transition: all 0.5s;
  cursor: pointer;
}
@media screen and (min-width: 768px) {
  .hamburger-button {
    display: none;
  }
}

.hamburger-button span {
  transition: all 0.5s;
}

.hamburger-button span:first-child {
  position: absolute;
  top: 0;
  width: 100%;
  height: 0.0625rem;
  background-color: #fff;
}

.hamburger-button span:nth-child(2) {
  position: absolute;
  top: 50%;
  width: 100%;
  height: 0.0625rem;
  background-color: #fff;
}

.hamburger-button span:nth-child(3) {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 0.0625rem;
  background-color: #fff;
}

/*
