}
*/
.hamburger-button.is-active span:first-child {
  -webkit-transform: translateY(0.8rem) rotate(-325deg);
  transform: translateY(0.8rem) rotate(-325deg);
  width: 123%;
}

.hamburger-button.is-active span:nth-child(2) {
  opacity: 0;
}

.hamburger-button.is-active span:nth-child(3) {
  -webkit-transform: translateY(-0.7rem) rotate(325deg);
  transform: translateY(-0.7rem) rotate(325deg);
  width: 123%;
}

