.page-navi {
  display: flex;
  justify-content: center;
  column-gap: 0.75rem;
}

.page-navi a {
  display: flex;
  border: 1px solid #408F95;
  width: 2.375rem;
  height: 2.375rem;
  line-height: 1;
  align-items: center;
  justify-content: center;
  color: #408F95;
  font-weight: 700;
  transition: 0.3s;
}

.page-navi a:hover {
  background-color: #408F95;
  color: #fff;
}

.page-navi span.current {
  display: flex;
  border: 1px solid #408F95;
  width: 2.375rem;
  height: 2.375rem;
  line-height: 1;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  background-color: #408F95;
  color: #fff;
}

.page-navi__prev {
  margin-right: 1.75rem;
  position: relative;
}

.page-navi__prev span::before {
  content: "";
  display: block;
  position: absolute;
  color: #408F95;
  width: 0.75rem;
  height: 0.0625rem;
  top: 60%;
  left: 48%;
  transform: translate(-50%) rotate(45deg);
  background-color: #408F95;
  z-index: 2;
}

.page-navi__prev span::after {
  content: "";
  display: block;
  position: absolute;
  color: #408F95;
  width: 0.75rem;
  height: 0.0625rem;
  top: 40%;
  left: 48%;
  transform: translate(-50%) rotate(-45deg);
  background-color: #408F95;
  z-index: 2;
}

.page-navi__prev:hover span::before {
  background-color: #fff;
}

.page-navi__prev:hover span::after {
  background-color: #fff;
}

.page-navi__next {
  margin-left: 1.75rem;
  position: relative;
}

.page-navi__next span::before {
  content: "";
  display: block;
  position: absolute;
  color: #408F95;
  width: 0.75rem;
  height: 0.0625rem;
  top: 60%;
  left: 48%;
  transform: translate(-50%) rotate(-45deg);
  background-color: #408F95;
  z-index: 2;
}

.page-navi__next span::after {
  content: "";
  display: block;
  position: absolute;
  color: #408F95;
  width: 0.75rem;
  height: 0.0625rem;
  top: 40%;
  left: 48%;
  transform: translate(-50%) rotate(45deg);
  background-color: #408F95;
  z-index: 2;
}

.page-navi__next:hover span::before {
  background-color: #fff;
}

.page-navi__next:hover span::after {
  background-color: #fff;
}

