    padding: 1.8125rem 1.75rem 1.8125rem 2.5rem;
    font-size: 1.25rem;
  }
}

.sub-faq__title::before,
.sub-faq__title::after {
  content: "";
  display: block;
  position: absolute;
  top: 50%;
  right: 1rem;
  width: 1.75rem;
  height: 0.0625rem;
  background-color: #fff;
}
