.voice-card {
  padding: 0;
  box-shadow: 2px 2px 10px 0 rgba(0, 0, 0, 0.25);
  background-color: #fff;
}

.voice-card__inner {
  padding: 0.8rem 1rem 1.6rem 1rem;
}
@media screen and (min-width: 768px) {
  .voice-card__inner {
    padding: 1.5rem;
  }
}

.voice-card__flex {
  display: flex;
  justify-content: left;
  column-gap: 0.75rem;
  align-items: end;
}
@media screen and (min-width: 768px) {
  .voice-card__flex {
    column-gap: 1rem;
  }
}

.voice-card__title-block {
  min-width: 9.3125rem;
  width: 100%;
}
@media screen and (min-width: 768px) {
  .voice-card__title-block {
    margin-top: 1.5rem;
    max-width: 17.5rem;
    width: 100%;
  }
}

.voice-card__subtitle {
  text-align: left;
}
@media screen and (min-width: 768px) {
  .voice-card__subtitle {
    display: flex;
    justify-content: space-between;
  }
}

.voice-card__subtitle p {
  font-size: 0.75rem;
  line-height: 1.5;
}
@media screen and (min-width: 768px) {
  .voice-card__subtitle p {
    line-height: 1;
    margin-top: 1.1875rem;
  }
}

.voice-card__subtitle span {
  font-size: 0.75rem;
  line-height: 1;
  padding: 0.2rem 0.7rem 0.5rem;
  border: 1px solid #408F95;
  color: #408F95;
}
@media screen and (min-width: 768px) {
  .voice-card__subtitle span {
    padding: 0.625rem 0.9375rem;
  }
}

.voice-card__title {
  margin-top: 0.6rem;
  font-size: 1rem;
  font-weight: 500;
  letter-spacing: normal;
  line-height: 1.5;
  border-bottom: 1px solid #408F95;
  padding-top: 0.1rem;
}
@media screen and (min-width: 768px) {
  .voice-card__title {
    margin-top: 0.5rem;
    font-size: 1.5rem;
  }
}

.voice-card__image {
  aspect-ratio: 151/117;
  overflow: hidden;
  height: auto;
  max-width: 9.4375rem;
  width: 100%;
  position: relative;
  margin-top: 0.625rem;
}
@media screen and (min-width: 768px) {
  .voice-card__image {
    aspect-ratio: 180/140;
    overflow: hidden;
    height: auto;
    max-width: 11.25rem;
    width: 100%;
    margin-top: 0;
  }
}

.voice-card__image .color {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  z-index: 999;
  background-color: #408F95;
}

.voice-card__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.voice-card__text-block {
  margin-top: 1rem;
}
@media screen and (min-width: 768px) {
  .voice-card__text-block {
    margin-top: 2.5rem;
  }
}

.voice-card__text {
  font-size: 1rem;
  line-height: 1.5;
  letter-spacing: normal;
}
@media screen and (min-width: 768px) {
  .voice-card__text {
    letter-spacing: normal;
  }
}

@media screen and (min-width: 768px) {
  .voice-cards {
    display: flex;
    justify-content: left;
    flex-wrap: wrap;
    row-gap: 5rem;
    column-gap: 2.5rem;
  }
}

@media screen and (min-width: 768px) {
  .voice-cards__card {
    width: calc((100% - 2.5rem) / 2);
  }
}

.voice-cards__card:not(:first-child) {
  margin-top: 2.5rem;
}
@media screen and (min-width: 768px) {
  .voice-cards__card:not(:first-child) {
    margin-top: 0;
  }
}

@media screen and (min-width: 768px) {
  .voice {
    position: relative;
  }
}

.voice__inner {
  max-width: 37.5rem;
}

.voice__cards {
  margin-top: 2.8rem;
  /*max-width: rem(600);
  width: 100%;
  margin: 0 auto;*/
}
@media screen and (min-width: 768px) {
  .voice__cards {
    margin-top: 2.0625rem;
    /*max-width: rem(1080);*/
  }
}

.voice__card:not(:first-child) {
  margin-top: 2.5rem;
}
@media screen and (min-width: 768px) {
  .voice__card:not(:first-child) {
    margin-top: 0;
  }
}

.voice__button {
  text-align: center;
  margin-top: 2.3rem;
}
@media screen and (min-width: 768px) {
  .voice__button {
    margin-top: 5rem;
  }
}

.voice__illust {
  display: none;
}
@media screen and (min-width: 768px) {
  .voice__illust {
    display: block;
    position: absolute;
    top: -7.5rem;
    left: calc(50% - min(47vw, 38.75rem));
    width: 18.8125rem;
    height: 8.625rem;
  }
}

.voice__illust2 {
  display: none;
}
@media screen and (min-width: 768px) {
  .voice__illust2 {
    display: block;
    position: absolute;
    bottom: -4rem;
    right: calc(50% - min(48vw, 34rem));
    width: 4.4375rem;
    height: 10.0625rem;
  }
}